<template>
  <div class="supporters-view noto-sans">
    <div class="head">서포터즈 조회하기</div>
    <div class="supporter-reg">
      <table class="team-info">
        <tr>
          <th>이름</th>
          <td>{{ supporter.fields.name }}</td>
        </tr>
        <tr>
          <th>학번</th>
          <td>{{ supporter.fields.student_id }}</td>
        </tr>
        <tr>
          <th>학과</th>
          <td>{{ supporter.fields.department }}</td>
        </tr>
        <tr>
          <th>연락처</th>
          <td>{{ supporter.fields.contact }}</td>
        </tr>
        <tr>
          <th>사이즈</th>
          <td>{{ size[supporter.fields.size] }}</td>
        </tr>
      </table>
      <div class="link-container">
        <router-link :to="{ name: 'supporters_check' }" class="link link1">목록으로</router-link>
        <router-link :to="{ name: 'supporters_check' }" class="link2">
          <button class="link" v-on:click="del">삭제하기</button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'supporters_view',
  data () {
    return {
      theRightPassword: '',
      certified: false,
      supporter: [],
      size: ['XS', 'S', 'M', 'L', 'XL', 'XXL']
    }
  },
  created () {
    let uri = '/api/supporters/complete/' + this.$route.params.id + '/'
    this.$http.get(uri)
      .then((response) => {
        this.supporter = JSON.parse(response.data)[0]
      })
  },
  methods: {
    del: function () {
      this.$http.delete('/api/supporters/' + this.$route.params.id + '/')
        .then((response) => {
          console.log('successful')
        })
    }
  }
}

</script>

<style scoped>
.head {
  font-size: 64px;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.classified {
  display: block;
  font-size: 28px;
}

.classified > input {
  font-size: 20px;
}

.classified > button {
  background-color: #555555;
  text-align: center;
  font-size: 20px;
  border: none;
  color: white;
  cursor: pointer;
  padding: 5px 11px;
}

.team-info > tr {
  height: 30px;
}

.team-info > tr > th {
  font-size: 32px;
  width: 230px;
}

.team-info > tr > td {
  font-size: 28px;
}

.supporter-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.supporter-info {
  border-radius: 10px;
  background-color: #efefef;
  margin-right: 10px;
  margin-top: 10px;
  width: 150px;
  padding: 5px 9px 5px 9px;
  text-align: center;
}

.supporter-info > li {
  font-size: 20px;
  font-weight: 300;
  margin: 0 auto;
}

.link {
  background-color: #555555;
  text-align: center;
  font-size: 22px;
  border: none;
  color: white;
  cursor: pointer;
  padding: 5px 11px;
}

.link-container {
  margin-top: 20px;
}

.link1 {
  float: left;
}

.link2 {
  float: right;
}
</style>



